import React from 'react';
import {NavLink, useHistory} from 'react-router-dom';
import './Nav.css';

function Nav({toggleIsUserOpen}) {
    let history = useHistory();
    

    return (
        <div className='nav-container'>
            <header>
              <h1>
                <i className="fas fa-video"></i>
                <span>Movie Center</span>
              </h1>

              <nav>
                  <ul>
                      <li><NavLink exact to="/" activeClassName="link-active">Home</NavLink></li>
                      <li><NavLink to="/about" activeClassName="link-active">About</NavLink></li>
                      <li><NavLink to="/signin" activeClassName="link-active">Sign in</NavLink></li>
                  </ul>
              </nav>

              <div className='btn-wrapper'>
                <button>User History</button>
              </div>

              <p>
                <span onClick={()=>history.push('/cart')} ><i className="fas fa-shopping-cart"></i></span>
                <span onClick={()=>toggleIsUserOpen()}><i className="fas fa-user"></i></span>
              </p>
            </header>
        </div> 
    )
}

export default Nav
