import React, {useContext, useState} from 'react';
import {useHistory} from 'react-router-dom';
import './Cart.css';
import {MovieContext} from '../components/MovieContext';

function Cart() {
    const [movies, setMovies] = useContext(MovieContext);
    const [_, toggleReloadPage] = useState(true);
    const history = useHistory();

    const setAmount = (action, index) => {
        let updateCart = movies;
        if(action === 'plus' && updateCart.cart[index].amount < 5){
            //console.log('plus')
            updateCart.cart[index].amount++; 
            setMovies(updateCart);
            toggleReloadPage(prev => !prev);
        }
        if(action === 'minus' && updateCart.cart[index].amount > 1){
            //console.log('minus')
            updateCart.cart[index].amount--; 
            setMovies(updateCart);
            toggleReloadPage(prev => !prev)
        }
    }

    const deleteItem = (item) => {
        let updateCart = movies;
        updateCart.cart = updateCart.cart.filter(movie => movie !== item);
        setMovies(updateCart);
        toggleReloadPage(prev => !prev)
    }

    return (
        <div className='cart-container'>
            <header>
                <h1><i className="fas fa-shopping-cart icon-resize"></i>{movies.isLogin ? `${movies.userInfo.first_name} ${movies.userInfo.last_name}'s Cart` : "You are not loggin in"}</h1>
                <p id='total'>Total: <span>{movies.cart.length === 0 ? 0 : movies.cart.reduce((acc, current)=> acc + current.amount*current.price, 0)}</span></p>
            </header>

            <main>
                <ul>
                {movies.cart.length === 0 ? <h4 style={{textAlign:'center', fontSize: "24px"}}>You haven't buy anything yet...</h4> :
                  movies.cart.map((item, index) => 
                  <li key={item.title} data-last={index+1 === movies.cart.length ? 'true':''}>
                    <section>
                        <img src={item.img} alt="" />
                        <h2>{item.title}</h2>
                        <p className="price"><span>${item.price}</span></p>
                        <p className="amount">Amount: <span>{item.amount}</span>
                            <button onClick={()=>setAmount('plus',index)} className={item.amount===5 ? "disable" : ""}>+</button>
                            <button onClick={()=>setAmount('minus',index)} className={item.amount===1 ? "disable" : ""}>–</button>
                        </p>
                        <p className="each-total-price">total: $<span>{item.amount * item.price}</span>
                            <button onClick={()=>deleteItem(item)}><i className="far fa-trash-alt" style={{fontSize:"14px"}}></i> Delete</button>
                        </p>
                    </section>
                  </li>                     
                  )}    
                </ul>
            </main> 
            <button onClick={()=>history.push('/')} className="exit-Btn">Exit</button>           
        </div>
    )
}

export default Cart
