import React, {useContext, useState} from 'react';
import './SignIn.css';
import {MovieContext} from '../components/MovieContext';
import axios from 'axios';

function SignIn() {
    const [movies, setMovies] = useContext(MovieContext);
    const [enteredFirstName, setEnteredFirstName] = useState('');
    const [enteredLastName, setEnteredLastName] = useState('');
    const [enteredPassword, setEnteredPassword] = useState('');
    const [reloadPage, toggleReloadPage] = useState(true);
    
    const handleEnterChange = (e) => {
        switch(e.target.id){
            case 'firstName': setEnteredFirstName(e.target.value); break;
            case 'lastName': setEnteredLastName(e.target.value); break;
            default: setEnteredPassword(e.target.value); break;
        }
    }

    const handleSignin = async() => {
        try{
            const data = await axios.get(`/find_user/${enteredFirstName}/${enteredLastName}/${enteredPassword}`)
            const result = data.data.result;
            if(result === 'no result'){
                console.log('no such user');
            }else{
              console.log(result);  
              setMovies({...movies, isLogin: true, userInfo: result});
              toggleReloadPage(prev => !prev);
            }
        }catch(err){
            console.log(err);
        }
    }

    const handleSignout = () => {
        setMovies({...movies, cart: [], isLogin: false});
    }

    return (
        <div className="signin-container">
        {movies.isLogin ? 
          <div>
              <h1>You are now Signing in as {`${movies.userInfo.first_name} ${movies.userInfo.last_name}`}</h1>
              <button onClick={()=>handleSignout()}>Sign out</button>
          </div>
        :
          <div>
              <h1>Please sign in</h1>
              <div>
                  <label for='firstName'>First Name: </label>
                  <input type='text' id='firstName' value={enteredFirstName} onChange={handleEnterChange}/><br />
                  <label for='lastName'>First Name: </label>
                  <input type='text' id='lastName' value={enteredLastName} onChange={handleEnterChange}/><br />
                  <label for='password'>Email: </label>
                  <input type='text' id='password' value={enteredPassword} onChange={handleEnterChange}/><br />
                  <button onClick={()=>handleSignin()}>Sign in</button>
              </div>
          </div>  
        }    
        </div>
    )
}

export default SignIn
